import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { LegislativeCommissionsPage } from './legislative-commissions.page';
import {ComponentsModule} from "../components.module";

const routes: Routes = [
  {
    path: '',
    component: LegislativeCommissionsPage,
      children:
          [
              {
                  path: 'tabInformationCommission',
                  children:
                      [
                          {
                              path: '',
                              loadChildren: '../tabs/tab-information-commission/tab-information-commission.module#TabInformationCommissionPageModule'
                          }
                      ]
              },
              {
                  path: 'tabMembersCommission',
                  children:
                      [
                          {
                              path: '',
                              loadChildren: '../tabs/tab-members-commission/tab-members-commission.module#TabMembersCommissionPageModule'
                          }
                      ]
              },
              {
                  path: '',
                  redirectTo: 'tabInformationCommission',
                  pathMatch: 'full'
              }
          ]
  }
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    RouterModule.forChild(routes),
      ComponentsModule
  ],
  declarations: [LegislativeCommissionsPage]
})
export class LegislativeCommissionsPageModule {}
